* {
  background-color: #2E2E2E;
}

nav {
  color: #FACC2E
}

h1 {
  margin-left: 0%;
  color: #FACC2E
}

hr {
  border-color: #FACC2E;
  margin-bottom: 20px;
}

a {
  text-decoration: none;
  color: white;
  margin: 15px;
}
.detail {
  border: 1px solid rgba(247, 196, 55, 0.3);
  border-radius: 10px;
  width: 450px;
  height: 100px;
  padding: 10px;
  text-decoration: none;
  color: inherit;
  display: flex;
  flex-direction: row;
  font-size: 18px;
  transition: border-color 0.3s ease
}

.detail:hover {
  border: 1px solid #FACC2E
}

/* .table {
  display: flex;
  flex-direction: column;
  align-items: center;
} */
.table {
  display: grid;
  place-items: center;
  /* 2열로 설정 */
  /* 각 셀 사이의 간격 설정 */
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
}

.row {
  display: flex;
  flex-direction: row;
}

.description {
  margin-left: 10px;
  display: flex;
  flex-direction: column;
}

.navbar {
  color: #FACC2E;
}

h2 {
  margin-top: 0px;
  color: white;
  font-size: 18px;
  font-weight: 100;
  margin-bottom: 5px;
  margin-left: 5px;
}

img {
  width: 100px;
  height: 100px;
}

p {
  margin: 0px;
  font-weight: 100;
  font-size: 13px;
  color: #eeeeeecb;
  margin-top: 3px;
  margin-left: 5px;
}
